var data =[
	[
		{ "country": "USA" }, [ "Adam Smith", "Anya Li", "Beth Anglin", "Carol Coughlin", "Charlie Witherspoon", "Chuck Shoomer", "Cory Micek", "David Loo", "Ernesto Ayala", "Jennifer Feng", "Jerrod Bennett", "Jimmy O'Tay", "Lisa Lucas", "Lucky Nguyen", "Monique Lindsay", "Rick Berzel", "Stephen Paras", "Will Smith"]
	]
]

var loadNames = function(context, data){
	var selection = context.selection;

	for (var i = 0, l = selection.length; i < l; i++) {
		var layer = selection[i];
		var randomName = data[ Math.floor(Math.random() * data.length) ];

		layer.stringValue = randomName;
		layer.name = randomName;
	}
};

export default function onRun(context){
	var names = data[0][1];
	loadNames(context, names);
}